
import { defineComponent } from "vue";
import { Waypoint } from "vue-waypoint";
import HomeView from "@/views/HomeView.vue";
import AboutView from "@/views/AboutView.vue";
import EnEView from "@/views/EnEView.vue";
import SkillView from "@/views/SkillView.vue";
import WorkView from "@/views/WorkView.vue";
import SpecailView from "@/views/SpecialExperience.vue";

export default defineComponent({
  components: {
    HomeView,
    AboutView,
    Waypoint,
    EnEView,
    SkillView,
    WorkView,
    SpecailView,
  },
  data() {
    return {
      isTop: true,
      navLists: [
        {
          name: "About ",
          path: "#about",
        },
        {
          name: "Experience & Education ",
          path: "#experienceAndEdu",
        },
        {
          name: "Skill ",
          path: "#skill",
        },
        {
          name: "Works ",
          path: "#works",
        },
        {
          name: "Special Experience ",
          path: "#specailExperience",
        },
        {
          name: "Download My CV ",
          path: "https://drive.google.com/uc?export=download&id=1icZVrE2vIzNF73tuznT6Rk7nlUQhGg59",
        },
      ],
    };
  },
  methods: {
    onchange(state: any) {
      console.log(state.going);
      if (state.going == "IN") {
        this.isTop = true;
      } else {
        this.isTop = false;
      }
    },
    setLang() {
      let currentLang = localStorage.getItem("set-lang");
      localStorage.setItem("set-lang", currentLang == "tw" ? "en" : "tw");
      location.reload();
    },
  },
});
